import {fetchSongMetadata} from 'stems/getStems.js';
import {getTransposeDiff} from 'stems/getTransposeDifference.js';
import {getZporeRemixes} from 'zpore/getZporeRemixes.js';
import {getAttributeMapping} from 'erc721/attributes.js';
import {INSTRUMENTS, BASS, DRUMS} from 'constants/stem-types.js';

export const multiplayer = async (songId, leak) => {
    // given songId we can grab that
    let song = await fetchSongMetadata(window.songId, true);
    let otherSong = leak.song; 
    let remix = leak;
    console.log("SONG DATA!!");
    console.log(song, otherSong);

    let songOffset = parseInt(song.attributes["start-time"])/1000;
    let otherSongOffset = parseInt(otherSong.attributes["start-time"])/1000;


    let {detune, bpm} = leak.attributes;
    let bpmRatio = otherSong.bpm / bpm;
    let adjustedOffset = bpmRatio * otherSongOffset;
    let offsetDifference = otherSongOffset - songOffset;
    console.log("DETUNE=%s BPM=%s", detune, bpm);

    // how many semitones we need to pitch this to match this current song
    // note: this remix is from this same song so it
    let semitones = detune / 100; 

    // say this sample is +6st then we need to pitch it down -6st
    let transpose = getTransposeDiff(song, otherSong) - semitones;
    if (transpose >= 12) {
        transpose = transpose % 12;
    } else if (transpose >= 6) {
        transpose -= 12;
    } else if (transpose < -6) {
        transpose += 12;
}

    let sound = remix.metadata.animation_url;

    console.log('adjusted offset=', adjustedOffset);
    // the information needed by the player to load this correctly
    let payload = {
        sound: sound,
        transpose,
        onset: adjustedOffset,
        volume: 0.5,
        bpm,
        stemTypes: [INSTRUMENTS, BASS, DRUMS] // what to replace
    };

    console.log(payload);
    // send this to the player and have it replace the instrumental
    window.playerRef.current.contentWindow.postMessage({
        type: "load-stem",
        body: payload
    }, "*");

};

window.multiplayer = multiplayer;
