import {NewStem} from 'decentralized/EthereumEvents.js';
import {NewSong, SongAttribute} from 'decentralized/EthereumEvents.js';
import BackendQuery from 'decentralized/BackendQuery.js';
import {STEMS_CONTRACT} from './contract.js';

export const fetchSongMetadata = async (songId) => {
   let query = new BackendQuery(STEMS_CONTRACT)
        .from(NewSong)
        .select("title")
       .select("artist")
    if (songId !== undefined) {
        query = query.where('songId', parseInt(songId));
    }

    let results = await query.executeQuery(true, true);

    if (songId !== undefined) { 
        console.log("songId=", songId);
        let songIds = [{songId: parseInt(songId)}];
        console.log('song ids=', songIds);
        let attributes = await getAttributesForSongs(songIds);
        console.log('attributes songId=', songId,  attributes);
        results[0].attributes = attributes[songId];
        return results[0];
    } else {
        let attributes = await getAttributesForSongs();
        console.log('attributes=', attributes);
        results.forEach(x => x.attributes = attributes[x.songId]);
        return results;
    }
};
export const getSongTitle = async (songId) => {
    return (await fetchSongMetadata(songId)) || {title: "unreleased", artist: "yo"};;
}

const S3_BASE = "https://spores-stems.s3.us-east-2.amazonaws.com/"
export const fetchStemIds = async (object) => {
    let ipfsHashes = Array.from(new Set([
        ...object.synths.map(x => "ipfs://" + x[1].slice(S3_BASE.length)),
        ...object.drums.map(x => "ipfs://" + x[1].slice(S3_BASE.length))
    ]));
    let query = new BackendQuery(STEMS_CONTRACT)
        .from(NewStem)
        .select("stemId")
        .whereIn("uri", ipfsHashes);

    let results = await query.executeQuery();
    let ipfsToStemId = {};
    for (let result of results) {
        ipfsToStemId[S3_BASE + result.uri.slice("ipfs://".length)] = result.stemId;
    }
    return ipfsToStemId;
};

export const computeStemsData = (object, ipfsToStemId) => {
    // now we need to determine the percentages
    let synthPercentages = computeForType(ipfsToStemId, object.synths);
    let drumPercentages = computeForType(ipfsToStemId, object.drums);
    let percentages = {
        ...synthPercentages,
        ...drumPercentages
    };

    delete percentages[undefined];

    let stemIds = Object.keys(percentages).map(x => parseInt(x));
    let stemPercentages = Object.values(percentages).map(Math.round);
    let data = {
        stemIds,
        percentages: stemPercentages
    };
    return data;
};

const computeForType = (ipfsToStemId, history) => {
    let totalTime = history[history.length - 1][2];
    let lastTime = undefined;
    let stemToTime = {};
    let lastIpfsHash = undefined;
    for (let [step, ipfsHash, time] of history.slice(1)) {
        let stemId = ipfsToStemId[lastIpfsHash];
        lastIpfsHash = ipfsHash;
        let _lastTime = lastTime || 0;
        let timeChunk = time - _lastTime;
        lastTime = time;
        if (!stemToTime[stemId]) {
            stemToTime[stemId] = 0;
        }
        stemToTime[stemId] += timeChunk;
    }

    let stemToPercentage = {};
    for (let stemId in stemToTime) {
        if (stemId !== undefined) {
            stemToPercentage[stemId] = 100*stemToTime[stemId]/totalTime;
        }
    }

    return stemToPercentage;
};

const getAttributesForSongs = async (songs) => {
    let query = new BackendQuery(STEMS_CONTRACT)
        .from(SongAttribute);
    if (songs) {
        let songIds = songs.map(x => x.songId);
        console.log('song ids=', songIds);
        query = query.whereIn("songId", songIds);
    }

    let results = await query.executeQuery(true, true);
    console.log("RESULTS=", results);
    let songToAttributes = {};
    for (let result of results) {
        let {songId, attributeType, attributeValue} = result;
        if (attributeValue === '\x01') {
            attributeValue = true;
        } else if (attributeValue === '\x00') {
            attributeValue = false;
        } else if (attributeValue === null) {
            attributeValue = false;
        }
        if (!songToAttributes[songId]) {
            songToAttributes[songId] = {};
        }
        if (songToAttributes[songId][attributeType]) {
            // skip if it exists
            continue;
        } 
        songToAttributes[songId][attributeType] = attributeValue;
    }
    console.log('song to attributes=', songToAttributes);
    return songToAttributes;
};
