import React, { useEffect, useCallback, useState} from 'react';
import CloseIcon from 'components/svg/CloseIcon.js';
import ENSName from 'components/ens/ENSName.js';
import {connect} from 'react-redux';
import './leak-controller.scss';
import CurveDiv from 'components/ux/CurveDiv.js';
import {walk, curve, relative, cut, radius} from 'components/ux/dsl.js';


const LeakController = ({hideLeaks, leak, playerRef}) => { 
    const DROP_ALGO = [
        [],
        [
            radius(3),
        ],
        [
            radius(5),
        ],
        [
            radius(5),
            walk(50),
            cut(10, 15, 30)
        ],
    ];

    const revert = useCallback(() => {
        playerRef.current.contentWindow.postMessage({
            type: "revert-stem",
            body: ""
        }, "*");
        hideLeaks();
    }, [playerRef]);

    return (
        <CurveDiv algo={DROP_ALGO} radius={24} fill="#5e008082" stroke="#2e2c2c" className="leak-controller fixed z-30">
          <div className="text-xs z-30 p-3">
            <div className="flex">
              {leak.song.title} 
            </div>
            <div className="flex">
              {leak.song.artist}
            </div>
          </div>
          <CloseIcon
            onClick={revert}
            className="close-icon">
          </CloseIcon>
        </CurveDiv>
    );
};

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LeakController);
